body{
  margin: 0;
  padding: 0;
  z-index: 1000;
  background-color: #0E0024;
}
@font-face {
    font-family: "NewWGL4Font V28 V1";
    src: url("https://db.onlinewebfonts.com/t/b02c391b2a71498e4ce8e8b6a3e972ad.eot");
    src: url("https://db.onlinewebfonts.com/t/b02c391b2a71498e4ce8e8b6a3e972ad.eot?#iefix")format("embedded-opentype"),
    url("https://db.onlinewebfonts.com/t/b02c391b2a71498e4ce8e8b6a3e972ad.woff2")format("woff2"),
    url("https://db.onlinewebfonts.com/t/b02c391b2a71498e4ce8e8b6a3e972ad.woff")format("woff"),
    url("https://db.onlinewebfonts.com/t/b02c391b2a71498e4ce8e8b6a3e972ad.ttf")format("truetype"),
    url("https://db.onlinewebfonts.com/t/b02c391b2a71498e4ce8e8b6a3e972ad.svg#NewWGL4Font V28 V1")format("svg");
}
@font-face {
    font-family: "Juventus Fans Bold";
    src: url("https://db.onlinewebfonts.com/t/b4ccc98add6cc60de17dbb589dca2164.eot");
    src: url("https://db.onlinewebfonts.com/t/b4ccc98add6cc60de17dbb589dca2164.eot?#iefix")format("embedded-opentype"),
    url("https://db.onlinewebfonts.com/t/b4ccc98add6cc60de17dbb589dca2164.woff2")format("woff2"),
    url("https://db.onlinewebfonts.com/t/b4ccc98add6cc60de17dbb589dca2164.woff")format("woff"),
    url("https://db.onlinewebfonts.com/t/b4ccc98add6cc60de17dbb589dca2164.ttf")format("truetype"),
    url("https://db.onlinewebfonts.com/t/b4ccc98add6cc60de17dbb589dca2164.svg#Juventus Fans Bold")format("svg");
}
.App{
  width: 100%;
  height: 100%;
}
.container{
width: 100%;
height: 100%;
overflow: hidden;
}
.top{
  display: flex;
  overflow: hidden;
  width: 100%;
}
.top_Left{
  flex:1;
  align-items: flex-start;
  justify-content: flex-start;
  margin-left: 2%;
   font-family: "NewWGL4Font V28 V1";
   color:#F6AF57;
}
.top_Right{
   align-items: flex-end;
  justify-content: flex-end;
   border-radius:80%;
   overflow: hidden;
   width: 300px;
   height: 300px;
margin-top: 8%;
margin-right: 18%;
border: #F6EBFF solid 5px;
}
.top_Right img{
width: 100%;
height: 100%;
object-fit: cover;

margin-top: 2px;
}


.middle{
  width: fit-content;
  height: fit-content;
  position: absolute;
  top: 10%;
  left: 15%;

  
}
.introText{
 font-size: 3rem;
 
  font-family: "Juventus Fans Bold";
  color: #F6AF57;
  width: fit-content;
  white-space: nowrap;
  overflow: hidden;
   animation: typing 1s steps(40, end);
   

}
.introText p{
  background-clip: text;
   -webkit-background-clip: text;
}
@keyframes typing {
  from { width: 0; }
  to { width: 50%; }
}
@keyframes Opa {
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
  
}
.opa{
  animation-name: opa;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  
}
.Aboutme{
  display: flex;
  text-align:justify;
margin-right: 30%;
font-family: monospace;
color: #F6EBFF;
margin-top: -3rem;
}
.button button{
background-color: #F6AF57;
border: none;
padding: 10px;
width: 5rem;
  font-family: "Juventus Fans Bold";
  color: #F6EBFF;
}
.button button:hover{
  color: #F6EBFF;
  background-color: #0E0024;
}

.bottom{
  margin-top: 3%;
  display: block;
  width: fit-content;
  align-items: center;
  margin-left: 10%;
    font-family: "Juventus Fans Bold";
    
}
.small_containers{
display: flex;
align-items: center;
justify-content: center;
  white-space: nowrap;

}
.small_containers p{
color: #F6EBFF;

}
.percent{
  margin-left:5px;
  width: 30rem;
  height: 0.7rem;
  background-color: #0e002475;
  border-radius: 1rem;
margin: 3px;
}
.percent-con{
  border: #f6af577f solid 1px;
   border-radius: 1rem;
   margin-left: 6px;
    margin-right: 6px;
}
.percent-bar{
background-color: #F6AF57;
 width: var(--percent);
  height: 0.7rem;
   border-radius: 1rem;
}
@media screen and (max-width:600px) {
  .middle{
    top: 40%;
  }
  .bottom{
 margin-top: 120%;
 font-size: 20px;
 margin-top: 1%;
 margin-left: -1%;
  }
  .percent{
    width: 5rem;
  }
}